var $ = require('jquery');
var Matter = require('matter-js');
//
// var canvas = document.createElement('game')
// var context = canvas.getContext('2d')

window.onload = function () {
  // $('#game-canvas')

  // console.log($('.game-canvas'))

  var engine = Matter.Engine.create()

  var render = Matter.Render.create({
      element: document.querySelector('.game-canvas'),
      options: {
        width: 1250,
        height: 550,
        background: '#d9d9d9',
        wireframeBackground: '#222',
        hasBounds: false,
        enabled: true,
        wireframes: true,
        showSleeping: true,
        showDebug: false,
        showBroadphase: false,
        showBounds: false,
        showVelocity: false,
        showCollisions: false,
        showAxes: false,
        showPositions: false,
        showAngleIndicator: false,
        showIds: false,
        showShadows: false
      },
      engine: engine
  });

  var boxA = Matter.Bodies.rectangle(400, 200, 80, 80)
  var boxB = Matter.Bodies.rectangle(450, 50, 80, 80)
  var ground = Matter.Bodies.rectangle(400, 610, 810, 60, { isStatic: true })

  Matter.World.add(engine.world, [boxA, boxB, ground])

  Matter.Engine.run(engine)

  Matter.Render.run(render)

  console.log($('#game-canvas'))
}


